 relative;\n  display: inline-block;\n  margin-right: 7px;\n}\n\n.aplayer-volume-button {\n  background: none;\n  border: none;\n  outline: none;\n  cursor: pointer;\n  color: #999;\n  font-size: 11px;\n  padding: 0;\n  transition: all 0.2s ease;\n  width: 15px;\n  height: 15px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n\n  &:hover {\n    color: #b7daff;\n  }\n}\n\n.aplayer-volume-bar-wrap {\n  position: absolute;\n  bottom: 15px;\n  right: -3px;\n  width: 25px;\n  height: 36px;\n  z-index: 99;\n  opacity: 0;\n  visibility: hidden;\n  transition: all 0.3s ease-in-out;\n}\n\n.aplayer-volume-wrap:hover .aplayer-volume-bar-wrap {\n  opacity: 1;\n  visibility: visible;\n}\n\n.aplayer-volume-bar {\n  position: relative;\n  width: 5px;\n  height: 100%;\n  background: #aaa;\n  cursor: pointer;\n  margin: 0 auto;\n  overflow: hidden;\n  border-radius: 2px;\n  user-select: none; // 防止拖动时选中文本\n  touch-action: none;\n  -webkit-user-drag: none;\n  -webkit-touch-callout: none;\n  -webkit-tap-highlight-color: transparent;\n}\n\n.aplayer-volume {\n  position: absolute;\n  bottom: 0;\n  right: 0;\n  width: 5px;\n  transition: all 0.3s ease;\n  background: #b7daff;\n  height: 70%;\n}\n\n// 移动端隐藏音量控制\n@media screen and (max-width: 768px) {\n  .aplayer-volume-wrap {\n    display: none !important;\n  }\n}\n"],"sourceRoot":""}]);
