      display: none;\n    }\n\n    .aplayer-miniswitcher {\n      .aplayer-icon {\n        transform: rotate(0);\n      }\n    }\n  }\n}\n\n.aplayer-body {\n  display: flex;\n  position: relative;\n  overflow: hidden;\n  height: 66px;\n  padding-right: 18px;\n}\n\n.aplayer-pic {\n  position: relative;\n  width: 66px;\n  height: 66px;\n  background-size: cover;\n  background-position: center;\n  transition: all 0.3s ease;\n  cursor: pointer;\n  flex-shrink: 0;\n  overflow: hidden;\n\n  .aplayer-pic-img {\n    width: 100%;\n    height: 100%;\n    object-fit: cover;\n    display: block;\n  }\n\n  .aplayer-button {\n    position: absolute;\n    display: flex;\n    align-items: center;\n    justify-content: center;\n    outline: none;\n    cursor: pointer;\n    color: #fff;\n    border-radius: 50%;\n    border: 2px solid rgba(255, 255, 255, 0.8);\n\n    top: 50%;\n    left: 50%;\n    transform: translate(-50%, -50%);\n    width: 30px;\n    height: 30px;\n    background: rgba(0, 0, 0, 0.2);\n    font-size: 16px;\n    transition: all 0.1s ease-out;\n\n    &:hover {\n      border-color: rgba(255, 255, 255, 1);\n    }\n\n    &.aplayer-button-playing {\n      top: auto;\n      left: auto;\n      bottom: 6px;\n      right: 6px;\n      transform: none;\n      width: 20px;\n      height: 20px;\n      background: transparent;\n      font-size: 10px;\n    }\n\n    .fa-play {\n      text-align: center;\n      height: 15px;\n      width: 8px;\n      font-size: 14px;\n    }\n\n    .fa-pause {\n      text-align: center;\n      height: 10px;\n      width: 12px;\n    }\n  }\n}\n\n.aplayer-info {\n  flex: 1;\n  display: flex;\n  flex-direction: column;\n  min-width: 0;\n  padding: 8px 15px 10px 15px;\n  justify-content: space-between;\n  border-top: 1px solid #e9e9e9;\n}\n\n.aplayer-info-top {\n  display: flex;\n  align-items: center;\n  flex-wrap: nowrap;\n}\n\n.aplayer-music {\n  flex: 1;\n  overflow: hidden;\n  margin-right: 15px;\n  min-width: 0;\n\n  .aplayer-title {\n    font-size: 14px;\n    font-weight: 400;\n    color: #212121;\n    margin: 0 0 2px 0;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    line-height: 1.1;\n  }\n\n  .aplayer-author {\n    font-size: 12px;\n    color: #666;\n    margin: 0;\n    overflow: hidden;\n    text-overflow: ellipsis;\n    white-space: nowrap;\n    line-height: 1.1;\n  }\n}\n\n.aplayer-controller {\n  display: flex;\n  align-items: center;\n  width: 100%;\n}\n\n.aplayer-time {\n  display: flex;\n  align-items: center;\n  color: #999;\n  font-size: 11px;\n  flex-shrink: 0;\n  white-space: nowrap;\n\n  .aplayer-time-inner {\n    margin-right: 7px;\n    font-variant-numeric: tabular-nums;\n  }\n\n  .aplayer-ptime,\n  .aplayer-dtime {\n    font-variant-numeric: tabular-nums;\n  }\n}\n\n.aplayer-order {\n  background: none;\n  border: none;\n  outline: none;\n  cursor: pointer;\n  color: #999;\n  font-size: 11px;\n  padding: 0;\n  transition: all 0.2s ease;\n  width: 15px;\n  height: 15px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  margin: 0 1px;\n\n  &:hover {\n    color: #b7daff;\n  }\n}\n\n.aplayer-miniswitcher {\n  position: absolute;\n  top: 0;\n  right: 0;\n  bottom: 0;\n  height: 100%;\n  background: #e6e6e6;\n  width: 18px;\n  border-radius: 0 2px 2px 0;\n\n  .aplayer-icon {\n    height: 100%;\n    width: 100%;\n    transform: rotate(180deg);\n    transition: all 0.3s ease;\n    border: none;\n    background-color: transparent;\n    outline: none;\n    cursor: pointer;\n    opacity: 0.8;\n    vertical-align: middle;\n    padding: 0;\n    font-size: 12px;\n    margin: 0;\n    display: inline-block;\n\n    svg {\n      width: 12px;\n      height: 12px;\n      fill: #666;\n    }\n  }\n}\n\n// 移动端适配 - 标准响应式断点\n@media screen and (max-width: 1024px) {\n  .aplayer {\n    max-width: calc(100vw - 40px);\n  }\n}\n\n@media screen and (max-width: 768px) {\n  .aplayer {\n    bottom: 80% !important;\n    max-width: calc(100vw - 20px);\n    min-width: auto;\n\n    &.aplayer-narrow {\n      bottom: 80% !important;\n    }\n  }\n}\n\n// 平板端调整\n@media screen and (max-width: 640px) {\n  .aplayer-body {\n    height: 58px;\n  }\n\n  .aplayer-pic {\n    width: 58px;\n    height: 58px;\n  }\n\n  .aplayer-info {\n    padding: 7px 12px 9px 12px;\n  }\n}\n\n// 移动端\n@media screen and (max-width: 480px) {\n  .aplayer-body {\n    height: 54px;\n  }\n\n  .aplayer-pic {\n    .aplayer-author {\n      font-size: 11px;\n    }\n  }\n\n  .aplayer-time {\n    .aplayer-time-inner {\n      margin-right: 5px;\n    }\n  }\n\n  .aplayer-order {\n    width: 13px;\n    height: 13px;\n    font-size: 10px;\n  }\n}\n\n// 小屏移动端\n@media screen and (max-width: 390px) {\n  .aplayer-body {\n    height: 50px;\n  }\n\n  .aplayer-pic {\n    width: 50px;\n    height: 50px;\n  }\n\n  .aplayer-info {\n    padding: 4px 10px 6px 10px;\n  }\n\n  .aplayer-music {\n    .aplayer-title {\n      font-size: 12px;\n    }\n\n    .aplayer-author {\n      font-size: 10px;\n    }\n  }\n}\n\n// 极小屏幕\n@media screen and (max-width: 320px) {\n  .aplayer-body {\n    height: 46px;\n  }\n\n  .aplayer-pic {\n    width: 46px;\n    height: 46px;\n  }\n\n  .aplayer-info {\n    padding: 3px 8px 5px 8px;\n  }\n\n  .aplayer-order {\n    display: none;\n  }\n}\n"],"sourceRoot":""}]);
